const navItemDropdown = document.querySelector('.nav__item-dropdown');
const navLinkDropdown = document.querySelector('.nav__link_dropdown');
const dropdown = document.querySelector('.dropdown');
const linkImg = document.querySelector('.nav__link-img');
const main = document.querySelector('.main');

navItemDropdown.addEventListener('click', () => {
   dropdown.classList.toggle('dropdown_visible');
   linkImg.classList.toggle('nav__link-img_rotate');
   navLinkDropdown.classList.toggle('nav__link_dropdown-active');
});

main.addEventListener('click', () => {
   dropdown.classList.remove('dropdown_visible');
   linkImg.classList.remove('nav__link-img_rotate');
   navLinkDropdown.classList.remove('nav__link_dropdown-active');
});