const btnUp = document.querySelector(".btn-up");

window.addEventListener("scroll", (()=>{
   window.scrollY > 500 ? btnUp.classList.remove("btn-up_hidden") : btnUp.classList.add("btn-up_hidden")
}
)),
btnUp.addEventListener("click", (()=>{
   window.scrollTo({
      top: 0,
      left: 0,
      behavior: "smooth"
   })
}))